@import url('https://fonts.googleapis.com/css2?family=Raleway&display=swap');
* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}

body, button {
    font-family: 'Raleway', sans-serif;
    font-weight: 600;
    padding: 10px;
    letter-spacing: 1px;
}

.container {
    margin: auto 0px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    width: 100%;
    overflow: hidden;
    border: 1px solid #ccc;
}



.navbar {
    display: flex;
    justify-content: space-between;
    overflow: hidden;
    border-right: 1px solid #ccc;
    background-color: #f1f1f1;
}

.navbar button {
    background-color: inherit;
    float: left;
    border: none;
    outline: none;
    cursor: pointer;
    padding: 10px;
    transition: 0.2s;
}

.navbar button:hover {
    background-color: #ddd;
}

.navbar button.active {
    background-color: #ccc;
}

.navbar-title, .live-head {
    padding: 10px;
}

.live-head {
    display: flex;
    justify-content: center;
}

.navbar-title {
    margin: auto 10px auto 5px;
    font-weight: 800;
    letter-spacing: 5px;
}

.navbar-content {
    display: none;
    border-top: none;
}

.htmlcontent {
    display: block;
}

.section {
    width: 50vw;
    display: flex;
    flex-direction: column;
}


@media screen and (max-width: 930px) {
    .container {
        display: grid;
        grid-template-areas: 'a a' 'c c' 'b b' 'd d';
    }
    .section {
        width: 100%;
        height: 50vh;
    }
    .nav-head {
        grid-area: a;
    }
    .live-head {
        grid-area: b;
    }
    .code {
        grid-area: c;
        overflow: hidden;
    }
    .live {
        grid-area: d;
    }
}

@media screen and (max-width: 500px) {
    .navbar {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        overflow: hidden;
        border-right: 1px solid #ccc;
        background-color: #f1f1f1;
    }
    .navbar-title {
        text-align: center;
    }
    .navbar-button {
        margin: 0px auto;
    }
}

@media screen and (max-width: 270px) {
    .navbar-button {
        display: flex;
        flex-direction: column;
    }
}